import React, { useEffect } from "react";
import useAuthContext from "../../contexts/AuthContext";
import { Link } from "react-router-dom";

const Navigacio = () => {
  const { navigation } = useAuthContext(); // getNavItems lekrse az AuthContextbl


  return (
    <nav className="navbar navbar-expand-sm bg-light">
      <div className="container-fluid">
        <ul className="navbar-nav">
          {
           navigation ? (
            // Ha van navigcis adat, azt dinamikusan rendereljk
            navigation.map( ( item, i ) => (
              <li className="navbar-item" key={i}>
                <Link className="nav-link" to={item.url}>
                  {item.megnevezes}
                </Link>
              </li>
            ) )
          ) : (
            // Ha mg nincs adat, vagy tltdik, akkor egy "loading" llapotot mutatunk
            <li className="navbar-item">
              <span className="nav-link">Loading...</span>
            </li>
          )}
        </ul>
      </div>
    </nav>
  );
};

export default Navigacio;
